@font-face {
  font-family: "Inter", sans-serif;
  src: url(../assets/font/Inter-VariableFont.ttf);
}
:root {
  --font-body: "Inter", sans-serif;
}

html, body {
  font-family: var(--font-body);
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --blue: hsl(207, 100%, 37%);
  --light-blue: hsl(201, 100%, 47%);
  --red: hsl(14, 87%, 58%);
  --body-text: hsl(0, 2%, 38%);
  --title-text: hsl(219, 100%, 8%);
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  width: 85%;
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.navbar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "ham logo ." "link link link";
  align-items: center;
  padding: 0.5em 1em;
}
.navbar .hamburger {
  grid-area: ham;
  width: 25px;
  height: 3px;
  background: var(--title-text);
  border-radius: 100px;
  transition: all 250ms;
}
.navbar .hamburger::after, .navbar .hamburger::before {
  display: block;
  content: "";
  background: var(--title-text);
  height: 3px;
  border-radius: 100px;
  width: 115%;
  transition: all 250ms;
}
.navbar .hamburger::after {
  transform: translateY(-11px);
}
.navbar .hamburger::before {
  transform: translateY(8px);
}
.navbar .nav-logo {
  grid-area: logo;
}
.navbar .nav-links {
  display: block;
  grid-area: link;
  list-style-type: none;
  text-align: center;
  padding-left: 0;
  max-height: 0;
  overflow: hidden;
  transition: all 250ms;
  margin-bottom: -0.51rem;
}
.navbar .nav-links .nav__link {
  color: var(--blue);
  font-weight: 600;
  padding: 0.5em 0;
  display: block;
  border-bottom: 1px solid rgba(0, 156, 240, 0.2);
}
@media (min-width: 40em) {
  .navbar {
    grid-template-areas: ". logo ." "link link link";
  }
  .navbar .nav-logo {
    text-align: center;
  }
  .navbar .nav-logo img {
    width: 40%;
    margin: auto;
  }
  .navbar .hamburger {
    display: none;
  }
  .navbar .nav-links {
    max-height: 1000px;
    margin-bottom: 0.5em;
  }
  .navbar .nav-links .nav__link {
    display: inline-block;
    margin: 0.2rem 0.5rem;
  }
}
@media (min-width: 71.875em) {
  .navbar .navbar {
    display: flex;
  }
}

.tag-line {
  background-image: linear-gradient(to bottom, #0068bd, #009cf0);
}
.tag-line .tag-line__container {
  position: relative;
  padding: 1.5em 1em;
}
.tag-line .tag-line__container .tag {
  font-size: 1.5rem;
  color: #eeeeee;
}
.tag-line .tag-line__container .tag b {
  color: #ffffff;
}
.tag-line .tag-line__container .join-button {
  display: inline-block;
  text-align: center;
  font-size: 1.3rem;
  background: white;
  color: var(--blue);
  font-weight: 600;
  padding: 0.5em 1em;
  margin-top: 1em;
}
.tag-line .tag-line__container .join-button:hover {
  color: var(--red);
}
.tag-line .tag-line__container span.join-button {
  background: var(--red);
  color: white;
}
.tag-line .tag-line__container span.join-button:hover {
  color: var(--light-blue);
}
@media (min-width: 71.875em) {
  .tag-line .tag-line__container {
    padding: 3em 1em;
    display: flex;
    flex-direction: row;
    gap: 2em;
  }
  .tag-line .tag-line__container .tag {
    width: 70%;
  }
  .tag-line .tag-line__container .col {
    width: 30%;
  }
}

.standards {
  padding: 2em 0;
}
.standards .standards__title {
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
  margin: 0 1em;
  color: var(--blue);
}
.standards .standards__description {
  margin: auto;
  margin-block: 1em;
  text-align: center;
}
.standards .standards__container .standards__standard {
  margin-inline: 1em;
  margin-block: 0.5em;
  border: 1px solid rgba(20, 20, 20, 0.1);
}
.standards .standards__container .standards__standard .standard__image {
  padding: 3em 1em;
  background-image: linear-gradient(to bottom, var(--blue), var(--light-blue));
  color: white;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
}
.standards .standards__container .standards__standard .standard__name {
  margin-block: 0.8em;
  font-weight: 700;
  margin-left: 0.5em;
}
.standards .standards__container .standards__standard .standard__read {
  background: var(--blue);
  color: white;
  font-weight: 700;
  display: inline-block;
  padding: 0.5em 1em;
  margin-bottom: 1em;
  margin-left: 0.5em;
}
.standards .standards__container .standards__standard .standard__read:hover {
  background: var(--light-blue);
}

.wa {
  animation: MoveUpDown 2s linear infinite;
  position: fixed;
  left: 20px;
  width: 40px;
  margin-bottom: 20px;
}

@keyframes MoveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 10px;
  }
}